import React from "react";
import { ParseDate } from "../../../../helpers/helper";

function TableStatus({ CurrentGuia = {} }) {
  return (
    <div>
      <div className="card card-body">
        <div className="col-sm-12 col-md-6">
          <h5 className="titleSection-form">Estados</h5>
        </div>
        <hr />
        <div className="row align-items-start">
          <div className="row table-cm table-responsive">
            <table className="table  nowrap">
              <thead>
                <tr className="header-table">
                  <th>Estado</th>
                  <th
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title="Estado"
                  >
                    Fecha
                  </th>
                  <th
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title="Fecha"
                  >
                    Rol
                  </th>
                  <th
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title="Origen"
                  >
                    Usuario
                  </th>
                  <th
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title="Destino"
                  >
                    Detalle
                  </th>
                </tr>
              </thead>

              <tbody>
                <tr key={1} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={"CREADO"}
                  >
                    CREADO
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      CurrentGuia?.creacion?.fecha === undefined
                        ? "------------"
                        : ParseDate(CurrentGuia?.creacion?.fecha)
                    }
                  >
                    {CurrentGuia?.creacion?.fecha === undefined
                      ? "------------"
                      : ParseDate(CurrentGuia?.creacion?.fecha)}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={CurrentGuia?.creacion?.rol ?? "------------"}
                  >
                    {CurrentGuia?.creacion?.rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={CurrentGuia?.creacion?.usuario ?? "------------"}
                  >
                    {CurrentGuia?.creacion?.usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="detalle"
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
                <tr key={2} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={"CREADO"}
                  >
                    PESO ACTULIZADO
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      CurrentGuia?.updatePeso?.fecha === undefined
                        ? "------------"
                        : ParseDate(
                            CurrentGuia?.updatePeso?.fecha ?? Date.now()
                          )
                    }
                  >
                    {CurrentGuia?.updatePeso?.fecha === undefined
                      ? ""
                      : ParseDate(CurrentGuia?.updatePeso?.fecha ?? Date.now())}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={CurrentGuia?.updatePeso?.rol ?? "------------"}
                  >
                    {CurrentGuia?.updatePeso?.rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={CurrentGuia?.updatePeso?.usuario ?? "------------"}
                  >
                    {CurrentGuia?.updatePeso?.usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="detalle"
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
                <tr key={3} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={"CREADO"}
                  >
                    AGREGADO A MANIFIESTO
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      CurrentGuia?.asignada?.fecha === undefined
                        ? "------------"
                        : ParseDate(CurrentGuia?.asignada?.fecha ?? Date.now())
                    }
                  >
                    {CurrentGuia?.asignada?.fecha === undefined
                      ? "------------"
                      : ParseDate(CurrentGuia?.asignada?.fecha ?? Date.now())}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={CurrentGuia?.asignada?.rol ?? "------------"}
                  >
                    {CurrentGuia?.asignada?.rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={CurrentGuia?.asignada?.usuario ?? "------------"}
                  >
                    {CurrentGuia?.asignada?.usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="detalle"
                      // onClick={() => printGuiaAction(CurrentGuia)}
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
                <tr key={4} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={"CREADO"}
                  >
                    RECEPCIÃN DE MANIFIESTO
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      CurrentGuia?.receptado?.fecha === undefined
                        ? "------------"
                        : ParseDate(CurrentGuia?.receptado?.fecha ?? Date.now())
                    }
                  >
                    {CurrentGuia?.receptado?.fecha === undefined
                      ? "------------"
                      : ParseDate(CurrentGuia?.receptado?.fecha ?? Date.now())}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={CurrentGuia?.receptado?.rol ?? "------------"}
                  >
                    {CurrentGuia?.receptado?.rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={CurrentGuia?.receptado?.usuario ?? "------------"}
                  >
                    {CurrentGuia?.receptado?.usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="detalle"
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
                <tr key={5} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={"CREADO"}
                  >
                    ASIGNADO A CARTERO
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      CurrentGuia?.asignada?.fecha === undefined
                        ? "------------"
                        : ParseDate(CurrentGuia?.asignada?.fecha ?? Date.now())
                    }
                  >
                    {CurrentGuia?.receptado?.fecha === undefined
                      ? "------------"
                      : ParseDate(CurrentGuia?.asignada?.fecha ?? Date.now())}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={CurrentGuia?.asignada?.rol ?? "------------"}
                  >
                    {CurrentGuia?.asignada?.rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={CurrentGuia?.asignada?.usuario ?? "------------"}
                  >
                    {CurrentGuia?.asignada?.usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="detalle"
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
                <tr key={6} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      CurrentGuia?.registroCorrespondencia?.novedad ??
                      "------------"
                    }
                  >
                    {CurrentGuia?.registroCorrespondencia?.novedad ??
                      "--- ENTREGA ---"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      CurrentGuia?.registroCorrespondencia?.fecha === undefined
                        ? "------------"
                        : ParseDate(
                            CurrentGuia?.registroCorrespondencia?.fecha ??
                              Date.now()
                          )
                    }
                  >
                    {CurrentGuia?.receptado?.fecha === undefined
                      ? "------------"
                      : ParseDate(
                          CurrentGuia?.registroCorrespondencia?.fecha ??
                            Date.now()
                        )}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      CurrentGuia?.registroCorrespondencia?.rol ??
                      "------------"
                    }
                  >
                    {CurrentGuia?.registroCorrespondencia?.rol ??
                      "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      CurrentGuia?.registroCorrespondencia?.usuario ??
                      "------------"
                    }
                  >
                    {CurrentGuia?.registroCorrespondencia?.usuario ??
                      "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="detalle"
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div
        className="modal fade"
        id="detail_status"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered modal-xl">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="staticBackdropLabel">
                Detalles de Estado
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>

            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Cancelar
              </button>
              <button
                type="submit"
                className=" btn btn-danger p-2"
                data-bs-dismiss="modal"
              >
                Enviar Solicitud
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TableStatus;
