import React, { useState } from "react";
import { useUpdateCheck } from "react-update-notification";
import { useToasts } from "react-toast-notifications";
import packageJson from "../../package.json";

export const NotificationContainer = () => {
  const { addToast } = useToasts();
  const [values, setvalues] = useState({
    stateNotification: true,
  });

  const { status, reloadPage } = useUpdateCheck({
    type: "interval",
    interval: 10000,
  });
  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: false,
    });
  };
  if (status === "checking" || status === "current") {
    // if (status === "checking" && values.stateNotification === true) {
    //   alert(`new version ${packageJson.version}`);
    //   setvalues({ ...values, stateNotification: false });
    // }
    return null;
  }

  return (
    <div>
      <button type="button" onClick={reloadPage}>
        Refresh to update the app
      </button>
    </div>
  );
};
