import React from "react";
import { DebounceInput } from "react-debounce-input";
import BuscarManifiestoHook from "../../../../hooks/buscar_manifiesto_hook";
import EstructuraManifiesto from "../../../dash-componets/estructura_manifiesto/estructura-manifiesto";
import Spinner from "../../../spinner/spinner";
import Dashfooter from "../../../dash-componets/footter/dashfooter";

function BuscarManifiestos() {
  const [
    values,
    msgError,
    fetching,
    currentPrintManifiesto,
    autoSearchManifiestoValue,
    searchManifiestoHanddler,
    searchManifiestoManual,
    autoSearchManifiestoAction,
    clean,
    searchManifiesto,
  ] = BuscarManifiestoHook();

  return (
    <div>
      <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 d-print-none scroll-section">
        <div className="container-fluid pt-4 mb-4 ">
          <h4 className="title-separtor">Buscar Manifiesto</h4>
          <hr />
          <div className="card card-body">
            <div className="row align-items-center mb-2">
              <div className="col-sm-12 col-md-12">
                <form>
                  <label className="form-label-input mb-4">
                    Buscar Manifiesto
                  </label>
                  <div className="input-group">
                    {autoSearchManifiestoValue === false ? (
                      <input
                        type="text"
                        className="form-control dropdown-toggle"
                        placeholder="Num. Manifiesto..."
                        id="top-search"
                        name="textSearch"
                        value={values?.textSearch}
                        onChange={searchManifiestoHanddler}
                      />
                    ) : (
                      <DebounceInput
                        minLength={0}
                        debounceTimeout={500}
                        type="text"
                        className="form-control dropdown-toggle"
                        placeholder="Num. Manifiesto..."
                        id="top-search"
                        name="textSearch"
                        value={values?.textSearch}
                        onChange={searchManifiesto}
                      />
                    )}
                    <span className="mdi mdi-magnify search-icon" />
                    {fetching ? (
                      <div className="container-spinner-buscar">
                        <Spinner />
                      </div>
                    ) : autoSearchManifiestoValue === false ? (
                      <button
                        className="btn-solid-secondary px-4 "
                        type="button"
                        onClickCapture={searchManifiestoManual}
                      >
                        Buscar
                      </button>
                    ) : (
                      <></>
                    )}
                  </div>
                </form>
                <div className="col text-start mt-2">
                  <div className="form-check form-switch">
                    BÃºsqueda AutomÃ¡tica
                    <input
                      className="form-check-input"
                      type="checkbox"
                      id="flexSwitchCheckChecked"
                      onClick={autoSearchManifiestoAction}
                      checked={autoSearchManifiestoValue}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="card card-body">
            {currentPrintManifiesto?.idManifiesto === "" ||
            currentPrintManifiesto?.idManifiesto === undefined ? (
              <p className="p-4">Esperando Manifiesto</p>
            ) : (
              <div>
                <div className="w-100 text-end">
                  <button
                    type="button"
                    className="btn-close p-2"
                    onClick={clean}
                  ></button>
                </div>
                <EstructuraManifiesto
                  manifiesto={currentPrintManifiesto}
                  last={true}
                  showmarca={false}
                />
                <button
                  type="button"
                  onClick={() => window.print()}
                  className="btn btn-success w-100"
                >
                  Imprimir Manifiesto
                </button>
              </div>
            )}
          </div>
        </div>
        <Dashfooter />
      </div>
      <div className="col-md-9 ms-sm-auto col-lg-10 d-none d-print-block">
        {currentPrintManifiesto?.idManifiesto === "" ||
        currentPrintManifiesto?.idManifiesto === undefined ? (
          <></>
        ) : (
          <EstructuraManifiesto
            manifiesto={currentPrintManifiesto}
            last={true}
          />
        )}
      </div>
    </div>
  );
}

export default BuscarManifiestos;
