import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useToasts } from "react-toast-notifications";
import {
  autoguiaManifiesto,
  autoSearchManifiesto,
  cleanManifiesto,
  clearManifiestoAction,
  findManifiesto,
} from "../redux/actions/manifiestos_actions";

function BuscarManifiestoHook() {
  const {
    manifiestos: {
      msgError,
      fetching,
      currentPrintManifiesto,
      autoSearchManifiestoValue,
    },
  } = useSelector((state) => state);
  const dispatch = useDispatch();

  const { addToast } = useToasts();
  const [values, setvalues] = useState({
    idManifiesto: "",
    idGuia: "",
    itemResividos: 0,
    malEncaminados: 0,
    pendientes: 0,
  });

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };

  const errorManifiesto = (error) => {
    return toastSms(error, "error");
  };
  const succesManifiesto = () => {
    return toastSms("Manifiesto encontrada", "success");
  };

  const searchManifiestoHanddler = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    setvalues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };
  const searchManifiestoManual = () => {
    if (values?.textSearch.trim()) {
      let res = values?.textSearch.replace(/ /g, "");
      dispatch(
        findManifiesto(res, succesManifiesto, errorManifiesto, "buscar")
      );
    } else {
      toastSms("Debes ingresar un Valor", "error");
    }
  };

  const searchManifiesto = (e) => {
    // // console.log("entro a la funcion");
    //! despues actualizar
    if (e.target.value !== "") {
      e.preventDefault();
    }
    setvalues({
      ...values,
      [e.target.name]: e.target.value,
    });

    if (e.target.value.trim()) {
      let res = e.target.value.replace(/ /g, "");
      dispatch(
        findManifiesto(res, succesManifiesto, errorManifiesto, "buscar")
      );
    } else {
      toastSms("Debes ingresar un Valor", "error");
    }
  };

  const clean = () => {
    return dispatch(clearManifiestoAction());
  };

  const autoSearchManifiestoAction = () => {
    console.log(autoSearchManifiestoValue);
    return dispatch(autoSearchManifiesto(!autoSearchManifiestoValue));
  };

  return [
    values,
    msgError,
    fetching,
    currentPrintManifiesto,
    autoSearchManifiestoValue,
    searchManifiestoHanddler,
    searchManifiestoManual,
    autoSearchManifiestoAction,
    clean,
    searchManifiesto,
  ];
}

export default BuscarManifiestoHook;
