//buscar guias por id para agregar al la lista de manifiestos
export const findGuiaManifiesto = (idGuia, showError, comprobarGuia) => {
  return async (dispatch) => {
    dispatch({
      type: GET_GUIA_MANIFIESTO,
    });
    try {
      let resp = await db
        .collection("guias")
        .doc(`${idGuia}`)
        .get()
        .then((doc) => {
          return doc.data();
        });
      // para agregregarse la guia debe de estar en peso actualizado // // console.log(resp.estado);
      if (resp === undefined) {
        showError("No existe guia con este nÃºmero de referencia");
        dispatch({
          type: GET_GUIA_MANIFIESTO_ERROR,
          payload: "",
        });
      } else if (resp?.estado === "agregado manifiesto") {
        if (resp?.numManifiesto?.length == 1) {
          showError(
            `Esta guÃ­a ya fue agregada a un Manifiesto ${resp?.numManifiesto[0]}`
          );
        } else {
          showError(
            `Esta guÃ­a ya fue agregada a un Manifiesto ${
              resp?.numManifiesto[resp?.numManifiesto.length - 1]
            }`
          );
        }
        dispatch({
          type: GET_GUIA_MANIFIESTO_ERROR,
          payload: "",
        });
      } else if (resp?.estado === "receptado") {
        showError(
          `Esta guÃ­a ya fue receptada en un Manifiesto ${resp?.addManifiesto?.idManifiesto}, no es posible agregar.`
        );
        dispatch({
          type: GET_GUIA_MANIFIESTO_ERROR,
          payload: "",
        });
      } else if (resp.updatePeso === undefined) {
        showError(
          `AÃºn no se a actualizado el peso de esta guÃ­a estado: "${resp?.estado}"`
        );
        dispatch({
          type: GET_GUIA_MANIFIESTO_ERROR,
          payload: "",
        });
      } else if (resp.updatePeso !== undefined) {
        await dispatch({
          type: GET_GUIA_MANIFIESTO_SUCCESS,
          payload: {},
        });
        comprobarGuia(resp?.destinatario?.provincia, resp);
      } else {
        showError("Error al agregar guia a manifiesto");
      }
    } catch (error) {
      dispatch({
        type: GET_GUIA_MANIFIESTO_ERROR,
        payload: "Error al buscar guÃ­as",
      });
      showError(`Error al agregar guia a manifiesto ${error.toString()}`);
    }
  };
};

export const crearManifiesto = (
  manifiesto,
  listGuias,
  mostrarAlertaOk,
  mostrarAlertaError,
  printGuias
) => {
  return async (dispatch) => {
    dispatch({
      type: CREATE_MANIFIESTO_DB,
    });
    try {
      listGuias.map(async (guia) => {
        let guiatemp;
        if (guia?.idManifiestos === undefined) {
          guiatemp = {
            ...guia,
            idManifiestos: [manifiesto.idManifiesto],
          };
        } else {
          guiatemp = {
            ...guia,
            idManifiestos: [...guia?.idManifiestos, manifiesto?.idManifiesto],
          };
        }
        await db.collection("guias").doc(`${guia.idGuia}`).update(guiatemp);
      });
      dispatch({
        type: CLEAN_MANIFIESTO,
        payload: manifiesto,
      });
      await db
        .collection("manifiestos")
        .doc(`${manifiesto.idManifiesto}`)
        .set(manifiesto);
      setTimeout(() => {
        printGuias();
        dispatch({
          type: CREATE_MANIFIESTO_DB_SUCCESS,
        });
      }, 2500);
      mostrarAlertaOk("Manifiesto Creado");
    } catch (error) {
      dispatch({
        type: CREATE_MANIFIESTO_DB_ERROR,
      });
      mostrarAlertaError(`error al crear manifiesto ${error}`);
    }
  };
};
