//resepcion de manifiestos
export const findManifiesto = (
  idManifiesto,
  successSms,
  errorSms,
  from = "recepcion"
) => {
  return async (dispatch) => {
    dispatch({
      type: GET_MANIFIESTO_DB,
    });
    try {
      const resp = await db
        .collection("manifiestos")
        .doc(`${idManifiesto}`)
        .get()
        .then((doc) => {
          return doc.data();
        });
      if (resp === undefined) {
        dispatch({
          type: GET_MANIFIESTO_FIND_ERROR,
        });
        errorSms("No existe un manifiesto registrado con ese id");
      } else {
        if (from === "recepcion") {
          if (resp?.estado === "receptado") {
            dispatch({
              type: GET_MANIFIESTO_FIND_ERROR,
              payload: "Error buscar manifiesto",
            });
            errorSms(
              `Este manifiesto ya fue receptado estado: ${resp?.estado}`
            );
          } else {
            dispatch({
              type: GET_MANIFIESTO_DB_SUCCESS,
              payload: resp,
            });
            successSms();
          }
        } else {
          dispatch({
            type: GET_MANIFIESTO_FIND_SUCCESS,
            payload: resp,
          });
          successSms();
        }
      }
    } catch (error) {
      dispatch({
        type: GET_MANIFIESTO_FIND_ERROR,
        payload: "Error buscar manifiesto",
      });
      errorSms(`Error al buscar: ${error}`);
    }
  };
};

