export const updateGuiaStatus = (ListGuias, idGuia, item) => {
  // console.log("desde action item a guardar");
  // console.log(item);
  let tempList = ListGuias.map((guia) => {
    if (guia.idGuia === idGuia) {
      // // console.log("guia encontrada");
      return item;
    } else {
      return guia;
    }
  });

  let listResividos = tempList.filter((item) =>
    item.estado.includes("receptado")
  ).length;
  let malEncamidanos = tempList.filter((item) =>
    item.estado.includes("mal encaminados")
  ).length;
  let pendientes = tempList.filter((item) =>
    item.estado.includes("creado")
  ).length;
  // console.log(`lista para guardar: ${tempList.map((e) => // console.log(e))}`);
  return (dispach) => {
    dispach({
      type: UPDATE_STATUS_GUIA_MANIFIESTO,
      payload: tempList,
    });
    dispach({
      type: SET_COMPROBACION_ENVIOS_RECINIDOS,
      payload: listResividos,
    });
    dispach({
      type: SET_COMPROBACION_ENVIOS_MAL_ENCAMINADOS,
      payload: malEncamidanos,
    });
    dispach({
      type: SET_COMPROBACION_ENVIOS_PENDIENTES,
      payload: pendientes,
    });
  };
};

