export const actualizarManifiestosyGuias = (
  manifiesto,
  successsms,
  errorsms,
  rolUser,
  user,
  provinciauser
) => {
  return async (dispatch) => {
    dispatch({
      type: UPDATE_MANIFIESTO_DB,
    });
    try {
      let itemlist = manifiesto?.listGuias?.map((itemGuia) => {
        if (itemGuia.estado === "creado") {
          itemGuia.estado = "pendiente";
          return itemGuia;
        } else {
          return itemGuia;
        }
      });

      await db
        .collection("manifiestos")
        .doc(`${manifiesto.idManifiesto}`)
        .update({
          listGuias: itemlist,
          estado: "receptado",
          receptado: {
            fecha: Date.now().toString(),
            usuario: user,
            rol: rolUser,
            provincia: provinciauser,
          },
        });

      itemlist?.map(async (guia) => {
        const resp =  await db.collection("guias").doc(`${guia?.idGuia}`).get().then((doc) => {
          return doc.data();
        });
        let tempguia;
        if (resp?.receptadoManifiesto === undefined) {
          tempguia = {
            ...guia,
            estado: "receptado",
            receptadoManifiesto: [
              {
                detalle: "receptado manifiesto",
                fecha: Date.now().toString(),
                usuario: user,
                rol: rolUser,
                provincia: provinciauser,
              },
            ],
          };
        } else {
          tempguia = {
            ...guia,
            estado: "receptado",
            receptadoManifiesto: 
            [
              ...resp?.receptadoManifiesto,
              {
                detalle: "Nuevamente Receptado en Manifiesto",
                fecha: Date.now().toString(),
                usuario: user,
                rol: rolUser,
                provincia: provinciauser,
              },
            ],
          };
        }
        await db.collection("guias").doc(`${guia?.idGuia}`).update(tempguia);
      });
      
      successsms();
      dispatch({
        type: UPDATE_MANIFIESTO_DB_SUCCESS,
      });
    } catch (error) {
      dispatch({
        type: UPDATE_MANIFIESTO_DB_ERROR,
        msgError: error,
      });
      errorsms(`error al receptar manifiesto ${error}`);
    }
  };
};

