export const fingGuiaAsignacion = (idGuia, showError, msgSuccess) => {
  return async (dispatch) => {
    dispatch({
      type: GET_GUIA_ASIGNACION_DB,
    });
    try {
      let resp = await db
        .collection("guias")
        .doc(`${idGuia}`)
        .get()
        .then((doc) => {
          return doc.data();
        });
      if (resp === undefined) {
        showError("No existe guia con este nÃºmero de referencia");
        dispatch({
          type: GET_GUIA_ASIGNACION_DB_ERROR,
          payload: "",
        });
      } else if (resp.estado === "asignado a cartero") {
        showError(
          `Guia ya Asignada a un Cartero, ID ASIGNACIÃN: ${resp?.idAsignacion}`
        );
        dispatch({
          type: GET_GUIA_ASIGNACION_DB_ERROR,
          payload: "",
        });
      } else if (
        resp.estado === "peso actualizado" ||
        resp.estado === "receptado"
      ) {
        await dispatch({
          type: GET_GUIA_ASIGNACION_DB_SUCCESS,
          payload: resp,
        });
        msgSuccess();
      } else {
        showError(
          `No es poible agregar esta guÃ­a estado actual: ${resp.estado}`
        );
        dispatch({
          type: GET_GUIA_ASIGNACION_DB_ERROR,
          payload: "",
        });
      }
    } catch (error) {
      dispatch({
        type: GET_GUIA_ASIGNACION_DB_ERROR,
      });
      showError(`Error al consultar guia: ${error}`);
    }
  };
};

