export const setAsignacionCorrespondencia = (
  asignacion,
  listaGuias,
  msgError,
  msgSuccess,
  print
) => {
  return async (dispatch) => {
    dispatch({
      type: SET_ASIGNACION_DB,
    });
    try {

      listaGuias.map(async (guia) => {
        let tempguia;
        if (guia?.asignada === undefined) {
          tempguia = {
            ...guia,
            estado: "asignado a cartero",
            idAsignacion: [asignacion?.idAsignacion],
            asignada: [
              {
                detalle: "Asignado a cartero",
                fecha: asignacion?.fechaCreacion,
                usuario: asignacion?.creadoPor,
                rol: asignacion?.rol,
                nomCartero: asignacion?.asignadoA,
              },
            ],
          };
        } else {
          tempguia = {
            ...guia,
            estado: "asignado a cartero",
            idAsignacion: [...guia?.idAsignacion, asignacion?.idAsignacion],
            asignada: [
              ...guia?.asignada,
              {
                detalle: "Nuevamente Asignado a Cartero",
                fecha: asignacion?.fechaCreacion,
                usuario: asignacion?.creadoPor,
                rol: asignacion?.rol,
                nomCartero: asignacion?.asignadoA,
              },
            ],
          };
        }
        await db.collection("guias").doc(`${guia?.idGuia}`).update(tempguia);
      });

      await db
        .collection("asignaciones")
        .doc(`${asignacion?.idAsignacion}`)
        .set(asignacion);

      dispatch({
        type: SET_ASIGNACION_DB_SUCCESS,
        payload: asignacion,
      });
      setTimeout(() => {
        print();
      }, 300);
      setTimeout(() => {
        msgSuccess();
      }, 700);
    } catch (error) {
      dispatch({
        type: SET_ASIGNACION_DB_ERROR,
        errorSms: error,
      });
      msgError(`Error AsignaciÃ³n: ${error}`);
    }
  };
};
