import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useToasts } from "react-toast-notifications";
import { removeSpaces } from "../helpers/helper";
import {
  addGuiaRegistro,
  autosearchregistroCorrespondencia,
  clearCurrentGuiaRegistro,
  findGuiaRegistro,
  registrarCorrespondenciaDb,
} from "../redux/actions/manifiestos_actions";
function RegistroCorrespondenciahook() {
  const {
    manifiestos: {
      msgError,
      fetching,
      listGuiasRegistro,
      currentGuiaRegistro,
      autoSearchRegistroCorrespondencia,
    },
    auth: { dataProfile },
  } = useSelector((state) => state);

  const rolUser =
    dataProfile?.rol === "admin"
      ? "Administrador"
      : dataProfile?.rol === "puntoVenta"
      ? "Punto de venta"
      : dataProfile?.rol === "sucursal"
      ? "Sucursal"
      : "Corporativo";

  const dispatch = useDispatch();

  const { addToast } = useToasts();

  const [values, setvalues] = useState({});

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };
  const handlerSearchManual = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    setvalues({
      ...values,
      idSearch: e.target.value,
    });
  };
  const SearchManual = () => {
    const success = () => toastSms("GuÃ­a Encontrada", "success");
    const errorSms = (sms) => toastSms(`${sms}`, "success");
    let res = removeSpaces(values.idSearch);
    console.log(`id Guia search ${res}`);
    if (res.trim()) {
      dispatch(findGuiaRegistro(res, success, errorSms));
    } else {
      toastSms("Debes ingresar un valor", "error");
    }
  };

  const searchGuia = (e) => {
    const success = () => toastSms("GuÃ­a Encontrada", "success");
    const errorSms = (sms) => toastSms(`${sms}`, "success");

    if (e.target.value !== "") {
      e.preventDefault();
    }
    let res = removeSpaces(e.target.value);
    if (res.trim()) {
      dispatch(findGuiaRegistro(res, success, errorSms));
    } else {
      toastSms("Debes ingresar un valor", "error");
    }
  };

  const handlechangeTate = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    setvalues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const addGuiaUpdate = () => {
    let tempGuia;
    if (currentGuiaRegistro?.registroCorrespondencia === undefined) {
      console.log(values?.novedad);
      tempGuia = {
        ...currentGuiaRegistro,
        estado: values?.novedad === "ENTREGADO" ? "entregado" : "no entregado",
        registroCorrespondencia: [
          {
            fecha: values?.date ?? Date.now().toString(),
            resibido: values?.resibido ?? "",
            ruc: values?.ruc ?? "",
            hora: values?.hora ?? Date.now().toString(),
            novedad: values?.novedad ?? "",
            descripcion_novedad: values?.descripcion_novedad ?? "",
            rol: rolUser ?? "",
            usuario: dataProfile?.email ?? "",
          },
        ],
      };
    } else {
      tempGuia = {
        ...currentGuiaRegistro,
        estado: values?.novedad === "ENTREGADO" ? "entregado" : "no entregado",
        registroCorrespondencia: [
          ...currentGuiaRegistro?.registroCorrespondencia,
          {
            fecha: values?.date ?? Date.now().toString(),
            resibido: values?.resibido ?? "",
            ruc: values?.ruc ?? "",
            hora: values?.hora ?? Date.now().toString(),
            novedad: values?.novedad ?? "",
            descripcion_novedad: values?.descripcion_novedad ?? "",
            rol: rolUser ?? "",
            usuario: dataProfile?.email ?? "",
          },
        ],
      };
    }

    let tempList = [...listGuiasRegistro, tempGuia];
    dispatch(addGuiaRegistro(tempList));
    dispatch(clearCurrentGuiaRegistro());
    toastSms("Guia Agregada", "success");
  };

  const deleteGuia = (idGuia) => {
    let listTemp = listGuiasRegistro?.filter((guia) => guia.idGuia !== idGuia);
    dispatch(addGuiaRegistro(listTemp));
  };

  const actualizarGuiasDb = () => {
    const susccess = () =>
      toastSms("Manifiesto actualizacon con exito", "success");
    const errorsms = (error) =>
      toastSms(`Error al actualizar ${error}`, "error");
    return dispatch(
      registrarCorrespondenciaDb(listGuiasRegistro, susccess, errorsms)
    );
  };

  const changeSearchswitch = () => {
    dispatch(
      autosearchregistroCorrespondencia(!autoSearchRegistroCorrespondencia)
    );
  };
  return [
    msgError,
    fetching,
    listGuiasRegistro,
    currentGuiaRegistro,
    values,
    autoSearchRegistroCorrespondencia,
    changeSearchswitch,
    actualizarGuiasDb,
    addGuiaUpdate,
    handlechangeTate,
    searchGuia,
    deleteGuia,
    handlerSearchManual,
    SearchManual,
  ];
}

export default RegistroCorrespondenciahook;
